import React, { useState } from 'react';
import { 
  Box, 
  Paper, 
  Typography, 
  ToggleButtonGroup, 
  ToggleButton,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  SelectChangeEvent
} from '@mui/material';
import {
  BarChart as BarChartIcon,
  PieChart as PieChartIcon,
  ShowChart as LineChartIcon,
  TableChart as TableChartIcon
} from '@mui/icons-material';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  LineChart,
  Line
} from 'recharts';

// D liu mu cho biu  doanh thu theo nhn vin
const REVENUE_DATA = [
  { name: 'Nguyn Vn A', value: 1200 },
  { name: 'Trn Th B', value: 950 },
  { name: 'L Vn C', value: 850 },
  { name: 'Phm Th D', value: 700 },
  { name: 'Hong Vn E', value: 500 },
];

// D liu mu cho biu  trng thi deal
const DEAL_STATUS_DATA = [
  { name: 'Lead', value: 45, color: '#f87171' },
  { name: 'Bo gi', value: 32, color: '#fbbf24' },
  { name: 'm phn', value: 18, color: '#60a5fa' },
  { name: 'Win', value: 38, color: '#34d399' },
  { name: 'Lose', value: 23, color: '#9ca3af' },
];

// D liu mu cho khch hng mi
const NEW_CUSTOMERS_DATA = [
  { name: 'Hm nay', value: 12 },
  { name: 'Hm qua', value: 10 },
  { name: '2 ngy trc', value: 8 },
  { name: '3 ngy trc', value: 15 },
  { name: '4 ngy trc', value: 7 },
];

// D liu mu cho doanh thu theo thng
const REVENUE_BY_MONTH_DATA = [
  { name: 'T1', value: 800 },
  { name: 'T2', value: 950 },
  { name: 'T3', value: 1100 },
  { name: 'T4', value: 1250 },
  { name: 'T5', value: 1400 },
  { name: 'T6', value: 1600 },
];

// D liu mu cho t l chuyn i
const CONVERSION_RATE_DATA = [
  { name: 'Chuyn i', value: 65, color: '#34d399' },
  { name: 'Khng chuyn i', value: 35, color: '#f87171' },
];

// D liu mu cho biu  doanh thu theo thng
const MONTHLY_REVENUE_DATA = [
  { name: 'T1', value: 1200 },
  { name: 'T2', value: 1900 },
  { name: 'T3', value: 1500 },
  { name: 'T4', value: 2200 },
  { name: 'T5', value: 1800 },
  { name: 'T6', value: 2400 },
  { name: 'T7', value: 2100 },
];

// Mu sc cho biu 
const COLORS = ['#3b82f6', '#10b981', '#f59e0b', '#8b5cf6', '#ef4444'];

// Loi biu 
type ChartType = 'bar' | 'pie' | 'line' | 'table';

// Loi thi gian
type TimeRange = 'today' | 'week' | 'month' | 'quarter' | 'year';

interface DynamicChartsProps {
  query: string;
}

const DynamicCharts: React.FC<DynamicChartsProps> = ({ query }) => {
  const [chartType, setChartType] = useState<ChartType>('bar');
  const [timeRange, setTimeRange] = useState<TimeRange>('month');

  const handleChartTypeChange = (
    event: React.MouseEvent<HTMLElement>,
    newChartType: ChartType | null,
  ) => {
    if (newChartType !== null) {
      setChartType(newChartType);
    }
  };

  const handleTimeRangeChange = (event: SelectChangeEvent) => {
    setTimeRange(event.target.value as TimeRange);
  };

  // Xc nh loi biu  v d liu da trn query
  const getChartTypeFromQuery = (query: string): { type: string; chartType: ChartType } => {
    const lowercaseQuery = query.toLowerCase();
    
    // Khch hng mi
    if (lowercaseQuery.includes('khch hng mi') || 
        (lowercaseQuery.includes('khch hng') && lowercaseQuery.includes('mi'))) {
      return { type: 'newCustomers', chartType: 'bar' };
    }
    
    // Doanh thu theo nhn vin
    else if (lowercaseQuery.includes('doanh thu') && lowercaseQuery.includes('nhn vin')) {
      return { type: 'revenueByEmployee', chartType: 'bar' };
    }
    
    // Trng thi deal
    else if ((lowercaseQuery.includes('deal') || lowercaseQuery.includes('giao dch')) && 
             (lowercaseQuery.includes('trng thi') || lowercaseQuery.includes('giai on'))) {
      return { type: 'dealStatus', chartType: 'pie' };
    }
    
    // Doanh thu theo thng
    else if (lowercaseQuery.includes('doanh thu') && 
             (lowercaseQuery.includes('thng') || lowercaseQuery.includes('thi gian'))) {
      return { type: 'revenueByMonth', chartType: 'line' };
    }
    
    // T l chuyn i
    else if (lowercaseQuery.includes('t l') || lowercaseQuery.includes('chuyn i')) {
      return { type: 'conversionRate', chartType: 'pie' };
    }
    
    // Mc nh hin th biu  doanh thu theo nhn vin
    return { type: 'revenueByEmployee', chartType: 'bar' };
  };

  const { type: chartDataType } = getChartTypeFromQuery(query || '');
  
  // T ng cp nht loi biu  da trn cu hi nu c query mi
  React.useEffect(() => {
    if (query) {
      const { chartType: newChartType } = getChartTypeFromQuery(query);
      setChartType(newChartType);
    }
  }, [query]);
  
  // Ly d liu ph hp vi loi biu 
  const getChartData = () => {
    switch (chartDataType) {
      case 'newCustomers':
        return NEW_CUSTOMERS_DATA;
      case 'dealStatus':
        return DEAL_STATUS_DATA;
      case 'revenueByMonth':
        return REVENUE_BY_MONTH_DATA;
      case 'conversionRate':
        return CONVERSION_RATE_DATA;
      case 'revenueByEmployee':
      default:
        return REVENUE_DATA;
    }
  };
  
  const chartData = getChartData();

  // Format gi tr tin t
  const formatValue = (value: number) => {
    return `${value} triu`;
  };

  return (
    <Paper elevation={2} sx={{ p: 3, borderRadius: 2, mb: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
        <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
           {query ? `Kt qu cho: "${query}"` : 'Phn tch d liu kinh doanh'}
        </Typography>
        
        <Box sx={{ display: 'flex', gap: 2 }}>
          <ToggleButtonGroup
            size="small"
            value={chartType}
            exclusive
            onChange={handleChartTypeChange}
            aria-label="chart type"
          >
            <ToggleButton value="bar" aria-label="bar chart">
              <BarChartIcon fontSize="small" />
            </ToggleButton>
            <ToggleButton value="pie" aria-label="pie chart">
              <PieChartIcon fontSize="small" />
            </ToggleButton>
            <ToggleButton value="line" aria-label="line chart">
              <LineChartIcon fontSize="small" />
            </ToggleButton>
            <ToggleButton value="table" aria-label="table">
              <TableChartIcon fontSize="small" />
            </ToggleButton>
          </ToggleButtonGroup>
          
          <FormControl size="small" sx={{ minWidth: 120 }}>
            <InputLabel id="time-range-label">Thi gian</InputLabel>
            <Select
              labelId="time-range-label"
              id="time-range"
              value={timeRange}
              label="Thi gian"
              onChange={handleTimeRangeChange}
            >
              <MenuItem value="today">Hm nay</MenuItem>
              <MenuItem value="week">Tun ny</MenuItem>
              <MenuItem value="month">Thng ny</MenuItem>
              <MenuItem value="quarter">Qu ny</MenuItem>
              <MenuItem value="year">Nm nay</MenuItem>
            </Select>
          </FormControl>
        </Box>
      </Box>

      <Box sx={{ height: 400, width: '100%' }}>
        {chartType === 'bar' && (
          <ResponsiveContainer width="100%" height="100%">
            <BarChart
              data={chartDataType === 'revenueByMonth' ? MONTHLY_REVENUE_DATA : REVENUE_DATA}
              margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip formatter={(value) => formatValue(value as number)} />
              <Legend />
              <Bar 
                dataKey="value" 
                name={chartDataType === 'revenueByMonth' ? 'Doanh thu theo thng' : 'Doanh thu theo nhn vin'} 
                fill="#3b82f6" 
              />
            </BarChart>
          </ResponsiveContainer>
        )}

        {chartType === 'pie' && (
          <ResponsiveContainer width="100%" height="100%">
            <PieChart>
              <Pie
                data={chartDataType === 'dealStatus' ? DEAL_STATUS_DATA : REVENUE_DATA}
                cx="50%"
                cy="50%"
                labelLine={false}
                outerRadius={150}
                fill="#8884d8"
                dataKey="value"
                nameKey="name"
                label={({ name, percent }) => `${name}: ${percent ? (percent * 100).toFixed(0) : 0}%`}
              >
                {chartData.map((entry, index) => (
                  <Cell 
                    key={`cell-${index}`} 
                    fill={(entry as any).color || COLORS[index % COLORS.length]} 
                  />
                ))}
              </Pie>
              <Tooltip formatter={(value) => formatValue(value as number)} />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        )}

        {chartType === 'line' && (
          <ResponsiveContainer width="100%" height="100%">
            <LineChart
              data={MONTHLY_REVENUE_DATA}
              margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip formatter={(value) => formatValue(value as number)} />
              <Legend />
              <Line 
                type="monotone" 
                dataKey="value" 
                name="Doanh thu theo thng" 
                stroke="#3b82f6" 
                activeDot={{ r: 8 }} 
              />
            </LineChart>
          </ResponsiveContainer>
        )}

        {chartType === 'table' && (
          <Box sx={{ height: '100%', overflow: 'auto' }}>
            <table style={{ width: '100%', borderCollapse: 'collapse' }}>
              <thead>
                <tr style={{ borderBottom: '2px solid #e2e8f0' }}>
                  <th style={{ padding: '12px 16px', textAlign: 'left' }}>Tn</th>
                  <th style={{ padding: '12px 16px', textAlign: 'right' }}>Gi tr</th>
                </tr>
              </thead>
              <tbody>
                {(chartDataType === 'dealStatus' ? DEAL_STATUS_DATA : 
                  chartDataType === 'revenueByMonth' ? MONTHLY_REVENUE_DATA : REVENUE_DATA)
                  .map((item, index) => (
                    <tr 
                      key={index} 
                      style={{ 
                        borderBottom: '1px solid #e2e8f0',
                        backgroundColor: index % 2 === 0 ? '#f8fafc' : 'white'
                      }}
                    >
                      <td style={{ padding: '12px 16px' }}>{item.name}</td>
                      <td style={{ padding: '12px 16px', textAlign: 'right' }}>
                        {formatValue(item.value)}
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          </Box>
        )}
      </Box>
    </Paper>
  );
};

export default DynamicCharts;